<template>
  <el-row>
    <el-col :span="24">
        <el-row class="text-align-right"><el-button type="primary" @click="showAddDialog">添加用户</el-button></el-row>
        <el-tabs v-model="activeName" @tab-click="onClickTab">
          <el-tab-pane label="全部" name="0">
            <el-table
              :data="tableData"
              style="width: 100%">
              <el-table-column
                prop="user_email"
                label="用户名">
              </el-table-column>
              <el-table-column
                prop="otp_secret"
                label="密钥">
              </el-table-column>
              <el-table-column
                prop="enable"
                label="状态">
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleSend(scope.$index, scope.row)">发送邮件</el-button>
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="已启用" name="1">
            <el-table
              :data="tableData"
              style="width: 100%">
              <el-table-column
                prop="user_email"
                label="用户名">
              </el-table-column>
              <el-table-column
                prop="otp_secret"
                label="密钥">
              </el-table-column>
              <el-table-column
                prop="enable"
                label="状态">
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleSend(scope.$index, scope.row)">发送邮件</el-button>
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="已禁用" name="2">
            <el-table
              :data="tableData"
              style="width: 100%">
              <el-table-column
                prop="user_email"
                label="用户名">
              </el-table-column>
              <el-table-column
                prop="otp_secret"
                label="密钥">
              </el-table-column>
              <el-table-column
                prop="enable"
                label="状态">
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleSend(scope.$index, scope.row)">发送邮件</el-button>
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
    </el-col>
    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      width="25%">
      <el-form ref="addUserForm" :model="addUserForm" label-width="80px" size="medium" class="add-user-form" :rules="rules">
        <el-form-item label="用户名" prop="user_email">
          <el-input v-model="addUserForm.user_email" placeholder="请输入邮箱" :disabled='isDisabled'></el-input>
        </el-form-item>
        <el-form-item label="密钥" class="item-secret" prop="otp_secret">
          <el-input v-model="addUserForm.otp_secret"></el-input>
          <el-button icon="el-icon-refresh" @click="onClickSecret"></el-button>
        </el-form-item>
        <el-form-item label="状态">
          <el-switch v-model="addUserForm.enable"></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onClickClose">取 消</el-button>
        <el-button type="primary" @click="onClickAdd('addUserForm')">添加</el-button>
      </span>
    </el-dialog>
  </el-row>
</template>
<script src="https://unpkg.com/axios/dist/axios.min.js"></script>
<script>
  export default {
    data() {
      return {
        activeName: '0',
        dialogVisible: false,
        url:'http://52.163.121.204:8000/',
        tableData:[],
        title:'新增',
        isDisabled:false,
        addUserForm: {
          user_email: '',
          otp_secret: '',
          enable: false
        },
        rules:{
          user_email:[
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur' }
          ],
          otp_secret:[
            { required: true, message: '请生成密钥', trigger: 'blur' }
          ]
        }
      };
    },
    mounted(){
      this.getData();
    },
    methods: {
      getData(activeName){
        activeName = activeName || 0;
        var t=this;
        t.axios.get('/api/user_list', {
            params: {
              mode:t.activeName
            }
          })
          .then(function (response) {
            if(response.data.return_code == '2'){
               t.tableData=response.data.result;
            }

          })
          .catch(function (error) {
            console.log(error);
          });
      },
      showAddDialog(){
        this.title='新增';
        this.addUserForm.enable=true;
        this.addUserForm.user_email = '';
        this.addUserForm.otp_secret = '';
        this.dialogVisible=true;
        this.isDisabled=false;
        this.getRandombase();
      },
      onClickTab(tab, event){
        this.getData(tab);
      },
      onClickClose() {
        this.dialogVisible = false;
      },
      onClickSecret() {
        this.getRandombase();
      },
      onClickAdd(addUserForm) {
        var t=this;

        this.$refs[addUserForm].validate((valid) => {
          if (valid) {
            t.addUserForm.enable = t.addUserForm.enable == true ? 'true' : 'false';
            t.axios.post('/api/user_add', JSON.stringify(t.addUserForm))
              .then(function (response) {
                if(response.data.return_code == '2'){
                  t.dialogVisible = false;
                  t.getData();
                }
              })
              .catch(function (error) {
                console.log(error);
              });
          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
      getRandombase(){
        var len = 16;
        var BASE32CHAR = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ234567';
        var maxPos = BASE32CHAR.length;
        var pwd = '';
        for (var i = 0; i < len; i++) {
          pwd += BASE32CHAR.charAt(Math.floor(Math.random() * maxPos));
        }
        this.addUserForm.otp_secret=pwd;
      },
      handleEdit(index,data){
        var t=this;
        t.title='修改';
        t.addUserForm.user_email = data.user_email;
        t.addUserForm.otp_secret = data.otp_secret;
        t.addUserForm.enable = data.enable == 'true' ? true : false;
        t.dialogVisible = true;
        t.isDisabled=true;
      },
      handleSend(index,data){
        var t=this;
        var oPostData={user_email:data.user_email};
        t.axios.post('/api/send_email', JSON.stringify(oPostData))
          .then(function (response) {
            if(response.data.return_code == '2'){
              t.$message({
                message: 'success',
                type: 'success'
              });
            }else{
              t.$message({
                message: 'error',
                type: 'warning'
              });
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      handleDelete(index,data){
         var t=this;
         var oPostData={user_email:data.user_email};
         t.axios.post('/api/user_del', JSON.stringify(oPostData))
           .then(function (response) {
             if(response.data.return_code == '2'){
               t.getData();
             }
           })
           .catch(function (error) {
             console.log(error);
           });
         }
      }
  }
</script>
<style>
.add-user-form{
  width:90%;
}
.item-secret .el-input{
  width:80%;
}
</style>



