<template>
  <el-row>
    <el-col :span="24">
        <el-row class="text-align-right"><el-button type="primary" @click="showAddDialog">添加服务器</el-button></el-row>
        <el-tabs v-model="activeName" @tab-click="onClickTab">
          <el-tab-pane label="全部" name="0">
            <el-table
              :data="tableData"
              style="width: 100%">
              <el-table-column
                prop="server_ip"
                label="Server ip">
              </el-table-column>
              <el-table-column
                prop="enable"
                label="Enable">
              </el-table-column>
              <el-table-column
                prop="custom"
                label="Custom">
              </el-table-column>
              <el-table-column
                prop="global_check"
                label="Global check">
              </el-table-column>
              <el-table-column
                prop="check_user"
                label="Check user">
              </el-table-column>
              <el-table-column
                prop="white_ip"
                label="White ip">
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="已启用" name="1">
            <el-table
              :data="tableData"
              style="width: 100%">
              <el-table-column
                prop="server_ip"
                label="Server ip">
              </el-table-column>
              <el-table-column
                prop="enable"
                label="Enable">
              </el-table-column>
              <el-table-column
                prop="custom"
                label="Custom">
              </el-table-column>
              <el-table-column
                prop="global_check"
                label="Global check">
              </el-table-column>
              <el-table-column
                prop="check_user"
                label="Check user">
              </el-table-column>
              <el-table-column
                prop="white_ip"
                label="White ip">
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="已禁用" name="2">
            <el-table
              :data="tableData"
              style="width: 100%">
              <el-table-column
                prop="server_ip"
                label="Server ip">
              </el-table-column>
              <el-table-column
                prop="enable"
                label="Enable">
              </el-table-column>
              <el-table-column
                prop="custom"
                label="Custom">
              </el-table-column>
              <el-table-column
                prop="global_check"
                label="Global check">
              </el-table-column>
              <el-table-column
                prop="check_user"
                label="Check user">
              </el-table-column>
              <el-table-column
                prop="white_ip"
                label="White ip">
              </el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
    </el-col>
    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      width="25%">
      <el-form ref="addServerForm" :model="addServerForm" label-width="100px" size="medium" class="add-user-form" :rules="rules">
        <el-form-item label="Server ip" prop="server_ip">
          <el-input v-model="addServerForm.server_ip" placeholder="请输入服务器IP" ></el-input>
        </el-form-item>
        <el-form-item label="Enable">
          <el-switch v-model="addServerForm.enable"></el-switch>
        </el-form-item>
        <el-form-item label="Custom">
          <el-switch v-model="addServerForm.custom" @change="onChangeCustom"></el-switch>
        </el-form-item>
        <div v-show="isShowCustom">
          <el-form-item label="Global check">
            <el-switch v-model="addServerForm.global_check" @change="onChangeCheck"></el-switch>
          </el-form-item>
          <el-form-item label="Check user" prop="check_user" v-show="!isShowCheck">
            <el-input v-model="addServerForm.check_user" placeholder="请用英文逗号“,”隔开"></el-input>
          </el-form-item>
          <el-form-item label="White ip" prop="white_ip">
            <el-input v-model="addServerForm.white_ip" placeholder="请用英文逗号“,”隔开"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="onClickClose">取 消</el-button>
        <el-button type="primary" @click="onClickAdd('addServerForm')">添加</el-button>
      </span>
    </el-dialog>
  </el-row>
</template>
<script src="https://unpkg.com/axios/dist/axios.min.js"></script>
<script>
  export default {
    data() {
      return {
        activeName: '0',
        dialogVisible: false,
        tableData:[],
        title:'新增',
        isShowCustom:false,
        isShowCheck:false,
        addServerForm: {
          server_ip: '',
          enable: false,
          custom: false,
          global_check: false,
          check_user: 'root',
          white_ip: 'YOU BYPASS IP'
        },
        rules:{
          server_ip:[
            { required: true, message: '请输入server ip', trigger: 'blur' }
          ],
        }
      };
    },
    mounted(){
      this.getData();
    },
    methods: {
      getData(activeName){
        activeName = activeName || 0;
        var t=this;
        t.axios.get('/api/server_list', {
            params: {
              mode:t.activeName
            }
          })
          .then(function (response) {
            if(response.data.return_code == '2'){
               t.tableData=response.data.result;
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      onChangeCustom(value){
        if(value){
          this.isShowCustom=true;
        }else{
          this.isShowCustom=false;
          this.addServerForm.global_check = false;
          this.addServerForm.check_user = 'root';
          this.addServerForm.white_ip = 'YOU BYPASS IP';
        }
      },
      onChangeCheck(value){
         if(value){
          this.isShowCheck=true;
        }else{
          this.isShowCheck=false;
          this.addServerForm.check_user = 'root';
        }
      },
      showAddDialog(){
        this.title='新增';
        this.addServerForm.enable=false;
        this.addServerForm.custom=false;
        this.addServerForm.global_check=false;
        this.addServerForm.server_ip = '';
        this.addServerForm.check_user = 'root';
        this.addServerForm.white_ip = 'YOU BYPASS IP';
        this.dialogVisible=true;
        this.isShowCustom=false;
      },
      onClickTab(tab, event){
        this.getData(tab);
      },
      onClickClose() {
        this.dialogVisible = false;
      },

      onClickAdd(addServerForm) {
        var t=this;

        this.$refs[addServerForm].validate((valid) => {
          if (valid) {
            t.addServerForm.enable = t.addServerForm.enable == true ? 'true' : 'false';
            t.addServerForm.custom = t.addServerForm.custom == true ? 'true' : 'false';
            t.addServerForm.global_check = t.addServerForm.global_check == true ? 'true' : 'false';
            t.axios.post('/api/server_add', JSON.stringify(t.addServerForm))
              .then(function (response) {
                if(response.data.return_code == '2'){
                  t.dialogVisible = false;
                  t.getData();
                }
              })
              .catch(function (error) {
                console.log(error);
              });
          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },

      handleEdit(index,data){
        var t=this;
        t.title='修改';
        t.addServerForm.server_ip = data.server_ip;
        t.addServerForm.check_user = data.check_user;
        t.addServerForm.white_ip = data.white_ip;
        t.addServerForm.enable = data.enable == 'true' ? true : false;
        t.addServerForm.custom = data.custom == 'true' ? true : false;
        t.addServerForm.global_check = data.global_check == 'true' ? true : false;
        t.dialogVisible = true;
        if(t.addServerForm.custom){
          t.isShowCustom=true;
        }else{
          t.isShowCustom=false;
        }
        if(t.addServerForm.global_check){
          t.isShowCheck=true;
        }else{
          t.isShowCheck=false;
        }
      },
      handleDelete(index,data){
         var t=this;
         var oPostData={server_ip:data.server_ip};
         t.axios.post('/api/server_del', JSON.stringify(oPostData))
           .then(function (response) {
             if(response.data.return_code == '2'){
               t.getData();
             }
           })
           .catch(function (error) {
             console.log(error);
           });
         }
      }
  }
</script>
<style>
.add-user-form{
  width:90%;
}
.item-secret .el-input{
  width:80%;
}
</style>



