<template>
  <el-row>
    <el-col :span="12">
      <el-form ref="addConfigForm" :model="addConfigForm" label-width="150px" size="large">
        <el-form-item label="default_enable">
          <el-switch v-model="addConfigForm.default_enable"></el-switch>
        </el-form-item>
        <el-form-item label="default_global_check">
          <el-switch v-model="addConfigForm.default_global_check"></el-switch>
        </el-form-item>
        <el-form-item label="default_check_user" prop="default_check_user">
          <el-input v-model="addConfigForm.default_check_user" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="default_white_ip">
          <el-input v-model="addConfigForm.default_white_ip" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="black_check" prop="black_check">
          <el-switch v-model="addConfigForm.black_check" @change="onChangeBlack"></el-switch>
        </el-form-item>
        <div  v-show="isShowBlack">
          <el-form-item label="black_check_time" prop="black_check_time">
            <el-input v-model="addConfigForm.black_check_time" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="black_check_count" prop="black_check_count">
            <el-input v-model="addConfigForm.black_check_count" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="black_deny_time" prop="black_deny_time">
            <el-input v-model="addConfigForm.black_deny_time" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="black_send_email" prop="black_send_email">
            <el-input v-model="addConfigForm.black_send_email" placeholder="请输入"></el-input>
          </el-form-item>
        </div>
        <el-form-item size="large">
          <el-button type="primary" @click="onClickUpdate">立即更新</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>
<script src="https://unpkg.com/axios/dist/axios.min.js"></script>
<script>
  export default {
    data() {
      return {
        title:'全局配置',
        isShowBlack:false,
        addConfigForm: {
          default_enable: false,
          default_global_check: false,
          default_check_user: '',
          default_white_ip: '',
          black_check_time: '',
          black_check_count: '',
          black_deny_time: '',
          black_check: false,
          black_send_email: '',
        },
        rules:{

        }
      };
    },
    mounted(){
      this.getData();
    },
    methods: {
      getData(activeName){
        var t=this;
        t.axios.get('/api/config_list', {})
          .then(function (response) {
            if(response.data.return_code == '2'){
               t.addConfigForm=t.formatValue(response.data.result,1);
               if(t.addConfigForm.black_check){
                 t.isShowBlack=true;
               }else{
                 t.isShowBlack=false;
               }
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      formatValue(data,type){
        if(data && data instanceof Object){
          if(type == 1){
            for(let item in data){
              if(data[item] == 'true'){
                data[item] = true;
              }
              if(data[item] == 'false'){
                data[item] = false;
              }
            }
            return data;
          }
          if(type == 2){
            for(let item in data){
              if(data[item] == true){
                data[item] = 'true';
              }
              if(data[item] == false){
                data[item] = 'false';
              }
            }
            return data;
          }
        }
        
      },
      onChangeBlack(value){
        if(value){
          this.isShowBlack=true;
        }else{
          this.isShowBlack=false;
        }
      },
      onClickUpdate() {
        var t=this;
        t.addConfigForm = t.formatValue(t.addConfigForm,2);
        console.log(t.addConfigForm);
        t.axios.post('/api/config_add', JSON.stringify(t.addConfigForm))
          .then(function (response) {
            if(response.data.return_code == '2'){
              t.getData();
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    }
  }
</script>
<style>

</style>



