<template>
  <el-row>
    <el-col :span="24">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="时间范围">
          <el-select v-model="formInline.day" placeholder="时间范围">
            <el-option label="当天内" value="1"></el-option>
            <el-option label="7天内" value="7"></el-option>
            <el-option label="30天内" value="50"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="类型">
          <el-select v-model="formInline.mode" placeholder="类型">
            <el-option label="全部" value="0"></el-option>
            <el-option label="成功" value="1"></el-option>
            <el-option label="失败" value="2"></el-option>
            <el-option label="错误" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getData">查询</el-button>
        </el-form-item>
      </el-form>
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column prop="server_ip" label="Server ip"></el-table-column>
        <el-table-column prop="client_ip" label="Client ip"></el-table-column>
        <el-table-column prop="result" label="Result"></el-table-column>
        <el-table-column prop="message" label="Message"></el-table-column>
        <el-table-column prop="check_user" label="Check user"></el-table-column>
        <el-table-column prop="time" label="time"></el-table-column>
        <el-table-column prop="user_email" label="User email"></el-table-column>
      </el-table>
    </el-col>
  </el-row>
</template>
<script src="https://unpkg.com/axios/dist/axios.min.js"></script>
<script>
  export default {
    data() {
      return {
        tableData:[],
        formInline:{
          day:'1',
          mode:'0'
        }
      };
    },
    mounted(){
      this.getData();
    },
    methods: {
      getData(){
        var t=this;
        t.axios.get('/api/log', {
            params: {
              mode:t.formInline.mode,
              day:t.formInline.day
            }
          })
          .then(function (response) {
            if(response.data.return_code == '2'){
               t.tableData=response.data.result;
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    }
  }
</script>
<style>

</style>



