<template>
  <div id="app">
    <el-container>
      <el-aside width="auto">
        <div class="title">JX-OTP</div>
        <el-menu
          router="router"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#409eff"
          :collapse="isCollapse"
          default-active="/user"
          class="el-menu-vertical">
          <el-menu-item index="/user">
            <i class="el-icon-menu"></i>
            <span slot="title">用户管理</span>
          </el-menu-item>
          <el-menu-item index="/server">
            <i class="el-icon-tickets"></i>
            <span slot="title">服务器管理</span>
          </el-menu-item>
          <el-menu-item index="/config">
            <i class="el-icon-setting"></i>
            <span slot="title">全局配置</span>
          </el-menu-item>
          <el-menu-item index="/log">
            <i class="el-icon-document"></i>
            <span slot="title">查看日志</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header>
          <div class="left link" @click="onClickFolding">
            <span><i class="el-icon-more"></i></span>
          </div>
          <div class="right link">
            <a href="/api/logout" target="_self" class="link">退出</a>
          </div>
        </el-header>
        <el-main>
          <router-view
            keep-alive
            transition="fade"
            transition-mode='out-in'></router-view>
        </el-main>
        <!--<el-footer>Footer</el-footer>-->
      </el-container>
    </el-container>
  </div>
</template>

<script>

export default {
  data() {
    return {
      isCollapse:false
    }
  },
  methods: {
    onClickFolding () {
      this.isCollapse=!this.isCollapse;
    }
  }
}
</script>

<style>
  #app {
    font-family: Helvetica, sans-serif;
  }
  .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }

  .el-aside {
    text-align: center;
    background: #4b5259;
  }

  .el-menu{
    text-align: left;
    border-right: none;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .title{
    height: 60px;
    line-height: 60px;
    color: #fff;
  }

  .el-container{
    min-height: 100vh;
  }
  .el-header{
    padding: 0;
    font-size: 12px;
    text-align:left;
    background-color: #545c64;
  }
  .left,.right{
    padding: 0 20px;
    cursor: pointer;
  }
  .el-menu-vertical:not(.el-menu--collapse) {
    width: 200px;
  }

</style>



