<template>
  <el-row>
    <el-col :span="6" :offset="9">
      <el-card class="box-card">
        <h1 class="text-align-center">登 录</h1>
        <div class="margin-4x"></div>
        <el-form ref="loginForm" :model="loginForm" label-width="80px" :rules="rules">
          <el-form-item label="登录名" prop="user">
            <el-input v-model="loginForm.user"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="loginForm.password" type="password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onClickLogin('loginForm')">登录</el-button>
            <el-button>取消</el-button>
          </el-form-item>
        </el-form>
      </el-card>

    </el-col>
  </el-row>
</template>
<script>
  export default {
    data() {
      return {
        loginForm:{
          user:'',
          password:''
        },
        rules:{
          user:[
            { required: true, message: '请输入登录名', trigger: 'blur' }
          ],
          password:[
            { required: true, message: '请输入密码', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      onClickLogin(loginForm) {
        var t=this;
        this.$refs[loginForm].validate((valid) => {
          if (valid) {
            t.axios.post('/api/login', t.loginForm)
              .then(function (response) {
                console.log(response);

              })
              .catch(function (error) {
                console.log(error);
              });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    }
  };
</script>
<style>

</style>



